.home{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
    max-width: 1500px;
}
.home_row{
    display: flex;
    flex-direction: row;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
}
.home_image{
    width:100%;
    z-index: -1;
    margin-bottom: -150px;
    mask-image:linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}